.layout {
  width: 100%;
  min-height: 100vh;
}
.layout .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  font-size: 1.2rem;
  font-weight: 300;
}
.layout .nav > div:first-child {
  font-size: 2rem;
  font-weight: 400;
  letter-spacing: 3px;
}
.layout .main {
  margin: auto;
  width: 40%;
  min-height: 70vh;
}
@media (max-width: 992px) {
  .layout .main {
    width: 90% !important;
  }
}
.layout .footer {
  text-align: center;
  padding: 20px;
  font-size: 0.9rem;
  color: #888;
}
.layout-light {
  border: 18px solid #00adb5;
}
.layout-dark {
  border: 18px solid #DEEAF3;
}
.layout-dark,
.layout-dark a {
  background-color: #00adb5 !important;
  color: #fff !important;
}
